import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';

@observer
class Team extends Component {
    render() {
        const { team } = this.props;
        const { teamStore } = this.props.stores;
        const workoutSummary = teamStore.findWorkoutSummary(team.id, 2020) || {};

        return (
            <div class='w-100 mb-3'>
                <div class='row'>
                    <div class='col-12 col-sm-8 col-lg-6 offset-sm-2 offset-lg-3 d-flex mb-0 position-relative'>
                        {/* {JSON.stringify(team, null, 4)} */}
                        <div class={`text-center border rounded-circle imageRounded`} style={team.image ? `background-image: url("${team.image.s3SmallLink}");` : ''}>
                            {!team.image && <i class='fas fa-users text-muted mt-3' style='font-size: 40px;' />}
                        </div>
                        <div class='flex-grow-1 pl-3'>
                            <a class='stretched-link' href={`/teams/${team.id}`}>
                                <h5>{team.name}</h5>
                            </a>
                            <small class='text-muted'>
                                {team && team.dogs && <span class=''><i class='fas fa-dog' /> {team.dogs.length}</span>} 
                                {team && team.members && <span class='ml-2'><i class='fas fa-user' /> {team.members.length}</span>}

                                {workoutSummary && workoutSummary.distanceKm > 0 && <span class='ml-2'><i class='fas fa-road' /> {util.format(workoutSummary.distanceKm, 0)}km</span>}
                                {workoutSummary && workoutSummary.elevation > 0 && <span class='ml-2'><i class='fas fa-mountain' /> {util.format(workoutSummary.elevation, 0)}m</span>}
                                {workoutSummary && workoutSummary.duration > 0 && <span class='ml-2'><i class='fas fa-clock' /> {util.secToHms(workoutSummary.duration)}</span>}
                                {workoutSummary && workoutSummary.speedAvg > 0 && <span class='ml-2'><i class='fas fa-tachometer-alt' /> {workoutSummary.speedAvg}km/t</span>}
                            </small>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default Team;
